}

.m-w-t-bakdrop {
  transition-property: opacity scale visibility;
  z-index: 1;
  z-index: 1;
  width: 100%;
  height: 100%;
  background: rgba(46, 47, 66, .4);
  justify-content: center;
  align-items: center;
  transition-duration: .2s;
  transition-timing-function: linear;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.m-w-t-window {
  max-width: 280px;
  min-height: auto;
  background: var(--color-black);
  border-radius: 16px;
