.tui-pagination * {
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.tui-ico-ellipsis,
.tui-ico-first,
.tui-ico-last,
.tui-ico-next,
.tui-ico-prev {
    overflow: hidden;
}

.tui-pagination {
    /* padding-top: 40px; */
    margin-top: 40px;
    /* margin-right: auto; */
    /* margin-left: auto; */
    height: 40px;
    display: flex;
    gap: 8px;
    /* margin: 14px 0 12px; */
    line-height: normal;
    text-align: center;
    font-size: 0;
    border-radius: 50%;
    padding: 12px 10px;
    justify-content: center;
}

.tui-pagination .tui-page-btn {
        width: 40px;
            height: 40px;
            color: #b7b7b7;
            vertical-align: middle;
            border: 1px solid #b7b7b7;
            border-radius: 50%;
            margin-left: 0;
            padding: 12px 11px;
            font-size: 14px;
            line-height: 14px;
            justify-content: center;
            text-decoration: none;
            display: flex;
            position: relative;
}



.tui-pagination .tui-is-selected,
.tui-pagination strong {
    color: #fff;
    background: linear-gradient(141.22deg, #FFC226 9.4%, #F84119 91.91%);
    /* border-color: #e4e9ee; */
    cursor: default;
    border: none;
    padding: 12px 10px;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    /* text-align: center; */

}

.tui-pagination .tui-first,
.tui-pagination .tui-prev,
.tui-pagination .tui-next,
.tui-pagination .tui-last,
.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
    overflow: hidden;
    height: 26px;
    padding: 0;
    font-size: 0;
    line-height: 26px;
    white-space: nowrap;
    font-weight: normal;
    border: 1px solid #ddd;
}

/* .tui-pagination .tui-prev-is-ellip {
    border-right: 0
} */

/* .tui-pagination .tui-next-is-ellip {
    border-left: 0
} */

/* .tui-pagination .tui-first+.tui-prev,
.tui-pagination .tui-next+.tui-last {
    border-left: 0
} */

.tui-pagination .tui-ico-first,
.tui-pagination .tui-ico-prev,
.tui-pagination .tui-ico-next,
.tui-pagination .tui-ico-last,
.tui-pagination .tui-ico-ellip {
    background: url(data:image/png;base64,) no-repeat 0 0;
    display: inline-block;
    vertical-align: middle;
    font-size: 0px;
    
}

.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
    padding: 0;
    line-height: 1;
    width: 40px;
    height: 40px;
    color: #B7B7B7;
}
.tui-page-btn.tui-next {
    width: 40px;
    height: 40px;
    border: none;
    display: none;
    
}
.tui-page-btn.tui-prev {
    width: 40px;
    height: 40px;
    border: none;
    display: none;

}

.tui-page-btn.tui-first {
    display: none;
}
.tui-page-btn.tui-last {
    display: none;
}


.tui-pagination .tui-ico-first {
    width: 11px;
    height: 10px;
    background-position: 0px 0px
}

.tui-pagination .tui-ico-prev {
    width: 6px;
    height: 10px;
    background-position: -26px 0px
}

.tui-pagination .tui-ico-next {
    color: #B7B7B7;
    width: 6px;
    height: 10px;
    background-position: -13px -24px
}

.tui-pagination .tui-ico-last {
    color: #B7B7B7;
    width: 11px;
    height: 10px;
    background-position: 0px -12px
}

.tui-pagination .tui-ico-ellip {
    width: 10px;
    height: 2px;
    margin-top: 16px;
    vertical-align: top;
    background-position: 0px -24px
}

.tui-pagination .tui-is-disabled:hover {
    /* background: #fff; */
    cursor: pointer;
}

.tui-pagination .tui-is-disabled .tui-ico-prev {
    background-position: -26px -12px
}

.tui-pagination .tui-is-disabled .tui-ico-first {
    background-position: -13px 0px
}

.tui-pagination .tui-is-disabled .tui-ico-next {
    background-position: -21px -24px
}

.tui-pagination .tui-is-disabled .tui-ico-last {
    background-position: -13px -12px
}

.tui-page-inputs {
    display: inline-block;
    vertical-align: top;
}

.tui-page-inputs .tui-page-input {
    display: inline-block;
    width: 14px;
    height: 16px;
    padding: 12px 9px;
    font-size: 12px;
    text-align: center;
    justify-content: center;
    color: #333;
    border: 0;
    outline: none;
}

.tui-page-inputs .tui-page-current {
    color: linear-gradient(141.22deg, #FFC226 9.4%, #F84119 91.91%);
}

.tui-page-separator {
    display: inline-block;
    font-size: 12px;
    height: 12px;
    padding: 8px 0 6px;
    /* vertical-align: top */
}
.tui-pagination .tui-page-btn:hover {
    background: linear-gradient(141.22deg, #FFC226 9.4%, #F84119 91.91%);
    border: none;
    }
.tui-pagination .tui-is-selected:hover {
    background: linear-gradient(141.22deg, #FFC226 9.4%, #F84119 91.91%);
border: none;
}
.tui-pagination .tui-is-disabled:hover {
    background: linear-gradient(141.22deg, #FFC226 9.4%, #F84119 91.91%);
    border: none;
}

